.items-list-root {
  display: grid;
  grid-template-columns: repeat(4, 0.25fr);
  grid-template-rows: auto;
}

.list-item {
  width: 17vmax;
  height: fit-content;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 35px;
  padding: 1vmax;
  margin: 1vmax;
  transition: background-color 250ms;
}

.list-item:hover {
  background-color: rgba(255, 255, 255, 0.07);
}

.list-item h4,
.list-item h5 {
  margin: 0;
  width: 100%;
  font-size: 0.95vmax;
}

.list-item h3 {
  margin-top: 0.2vmax;
  font-style: italic;
  font-size: 1.5vmax;
}

.list-item h3,
.list-item h4,
.list-item h5 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media screen and (max-width: 1150px) {
  .items-list-root {
    display: inline-flex;
    overflow: scroll;
    width: 100%;
  }

  .list-item {
    width: 91%;
    padding: 20px;
  }
}

@media screen and (max-width: 1400px) {
  .items-list-root {
    grid-template-columns: 0.5fr 0.5fr;
  }

  .list-item {
    width: 36vmax;
    padding: 20px;
  }

  .list-item h4,
  .list-item h5 {
    font-size: max(15px, 1vmax);
  }

  .list-item h3 {
    margin-top: 0.2vmax;
    font-style: italic;
    font-size: max(20px, 1.75vmax);
  }
}
